body{
    margin: auto;
}
#header{
    width:99%;
    height:50px;
    background-color: rgb(248, 248, 73);
    margin-left:17px;
    margin-right:17px ;
}
.dis{
    display: inline-block;
    float:right;
    padding:2px;
    border:1px solid rgb(189, 189, 211);
    font-size: 27px;
    margin-left: 20px;
    margin-right:19px ;
}
span{
    font-size:30px;
}
.CC{
    height:190px;
    width:70%;
    display: inline-block;
    margin: 20px 10px ;
    box-shadow: 0px 3px 7px black;
}
.CC1{
    width:30%;
    float: left;
}
.CC2{
    width:30%;
    float: left;
    margin-top: 13px;
}
.Heading{
    text-align:center;
    color:#0e1b36;
    font-size:20px;
    font-weight: bold;
}
.SubHeading{
    text-align: center;
    color:gray;
    font-size: 15px;
    line-height: 1.1;
}

.CC:hover{
    transform: scale(0.98,0.98);
    background-color:rgb(242, 235, 247);
}
img{
    height:190px;
    width:200px;
}
.logos{
    text-decoration: none;
    font-size: 30px;
    color: black;
    margin-left: 13px;
}
.load{
    text-align: center;
    margin-top:16%;
    margin-left: -100px;
}